nav {
  margin-top: 2%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.main {
  text-align: center;
  width: 90%; }
  .main button {
    background: linear-gradient(white, green); }

.container {
  width: 90%;
  margin: 0 auto; }

.height5vh {
  height: 5vh; }

.btn {
  padding: 2%;
  width: 33%;
  font-weight: bold;
  background: linear-gradient(#fff, #0000D6); }

#pixelsToPercents, #pixelsToPercentsHeight {
  margin-top: 1%; }

#numberPixelsValue, #numberPercentsValue, #numberPixelsValueHeight, #numberPercentsValueHeight {
  width: 75%;
  height: 20px; }

#calculatePixelsWidth, #calculatePixelsHeight, #calculatePercentsWidth, #calculatePercentsHeight {
  margin-top: 1%;
  padding: 2%;
  width: 25%;
  font-weight: bold; }

#scoreWidth, #scoreHeight {
  color: #000080;
  font-size: 1.1em;
  font-weight: bold; }

input {
  text-align: center; }

#showHide {
  transform: scale(0.75); }

#loading {
  margin: 25vh;
  font: 3em bold;
  text-shadow: 1px 1px white;
  animation: anim 3s;
  animation-fill-mode: forwards; }

@keyframes anim {
  0% {
    color: red; }
  100% {
    color: green; } }

@media (min-width: 1024px) {
  .btn {
    padding: 1%;
    width: 15%; }
  #numberPixelsValue, #numberPercentsValue, #numberPixelsValueHeight, #numberPercentsValueHeight {
    width: 75%;
    height: 20px; }
  #calculatePixelsWidth, #calculatePixelsHeight, #calculatePercentsWidth, #calculatePercentsHeight {
    padding: 1%;
    width: 30%; }
  #left, #right {
    width: 49%; }
  .main {
    display: flex;
    justify-content: space-between; }
  #loading {
    margin: 25vh 0 0 50%;
    font: 5em bold;
    text-shadow: 1px 1px white;
    animation: anim 3s;
    animation-fill-mode: forwards; } }

@media (min-width: 1366px) {
  .btn {
    padding: 1%;
    width: 15%; }
  #loading {
    margin-top: 35vh 0 0 75%;
    font: 5em bold;
    text-shadow: 1px 1px white;
    animation: anim 3s;
    animation-fill-mode: forwards; } }

* {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%;
  height: 100%; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

body {
  background-color: lightgray; }

header, footer, nav, #main {
  width: 100%; }

header {
  text-align: center; }
  header span {
    color: #0000D6;
    font-weight: bold; }
